import React, { Component } from 'react'
import styled from "@emotion/styled/macro";
import "./Job.css"

class Job extends Component {

    constructor() {
        super();
        this.state = {
            isHovering: false,
        };
    }

    render() {

        const Image = ({ className }) => (
            <img src={this.props.logo} className={className} />
        )

        const Default = styled.div({
            height: "100%",
            width: "100%",
            opacity: 1,
            transition: "opacity 350ms ease",
        });

        const DefaultContent = () => (
            <div className="center-holder w-100">

                <Image className="logo-img" />
                <h5>{this.props.companyName}</h5>
                <p>
                    {this.props.positionName}
                </p>
                <p><i>{this.props.date}</i></p>
            </div>
        )

        const DisplayOver = styled.div({
            height: "100%",
            left: "0",
            position: "absolute",
            top: "0",
            width: "100%",
            zIndex: 2,
            transition: "background-color 350ms ease",
            backgroundColor: "transparent"
        });

        const Hover = styled.div({
            height: "100%",
            width: "100%",
            opacity: 0,
            zIndex: 1000000,
            display: "flex",
            transform: "translate3d(0,50px,0)",
            transition: "opacity 350ms ease",
            alignItems: "center",
        });

        const HoverContent = () => (
            <div className="center-holder w-100">
                <h5>{this.props.companyName}</h5>
                <p>
                    {this.props.positionName}
                </p>
                <p><i>{this.props.date}</i></p>

                <button onClick={this.redirectToExperience} className="job-btn-dark">Learn more</button>
            </div>
        )

        const Background = styled.div({
            [`:hover ${Default}`]: {
                opacity: 0,
                display: "none",
            },
            [`:hover ${Hover}`]: {
                display: "flex",
                opacity: 1,
                transform: "translate3d(0,0,0)",
            },
        });

        return (

            <div className="job col-12 col-sm-6 d-inline-block">


                <Background>

                    <DisplayOver>

                        {/* <img src={this.props.logo} className="logo-img" /> */}
                        <Default>

                            {/* <div className="vertical-center"> */}
                            <DefaultContent />
                            {/* </div> */}
                        </Default>
                        <Hover>

                            {/* <div className="vertical-center"> */}
                            <HoverContent />
                            {/* </div> */}
                        </Hover>
                    </DisplayOver>
                </Background>
                {/* <div className="about-text d-inline job-card">
                        <h5>{this.props.companyName}</h5>
                        <p>
                            {this.props.positionName}
                        </p>
                        <p><i>{this.props.date}</i></p>
                    </div>
            

                    <div className="about-text d-inline job-card">
                        <h5>{this.props.companyName}</h5>
                    </div> */}
            </div >
        )
    }
}

export default Job