import React, { Component } from 'react'
import Skills from './Skills'
import About from './About'
import Employment from './Employment'
import "./Summary.css"

class Landing extends Component {

    render() {

        return (
            <div id="summary-main" className="mw-100">
                <div className="col-12 col-lg-3 float-left overflow-hidden p-0 sidebar">
                    <Skills />
                </div>
                <div className="p-0 minh-100">
                    <About />
                    <Employment />
                </div>
            </div>
        )
    }
}

export default Landing