import React, { Component } from 'react'
import "./About.css"

class About extends Component {

    render() {

        let photo = require("./../img/andy_photo.jpg");

        return (
            <main id="about-main" className="">

                <div className="d-inline">

                <div className="content-panel">

                    <div>
                        <h2 className="heading-title">About</h2>
                    </div>

                    <div id="about-text">

                        <div>
                            <img className="about-photo" src={photo} />
                        </div>
                        <p>
                            I am a junior Computer Engineering (Software Engineering Track) student at the University of Texas at Austin.
                            I enjoy creating meaningful projects, and I am excited to share some with you.
                        </p>
                    </div>
                </div>
                </div>
            </main>
        )
    }
}

export default About