import React, { Component } from 'react'

class LandingCanvas extends Component {

    constructor() {

        super();

        this.state = {

            // dot variables
            dots: [],
            minSize: 1,
            maxSize: 3,
            minSpeed: .3,
            maxSpeed: 2,
            minAlpha: .4,
            maxAlpha: .7,
            colors: ["255, 255, 255", "122, 255, 117", "128, 255, 223", "255, 141, 128", "157, 50, 250"],
            numDots: 30,
            bufferDistance: 50, // the number of pixels from the edges from the screen from which the dots "bounce",

            // for removing the dots later
            intervalId: { dots: null },
        };

        this.handleResize = this.handleResize.bind(this);
        this.generateDots = this.generateDots.bind(this);
        this.runDots = this.runDots.bind(this);
        this.sortDots = this.sortDots.bind(this);
    }

    componentDidMount() {

        let canvas = document.getElementById("background_canvas");
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;

        this.setState({numDots: 30 + Math.floor(canvas.width * canvas.height / 14000)}, ()=> {
            this.generateDots();
            this.runDots();
        });

        window.addEventListener('resize', this.handleResize);
    }

    handleResize() {
        let canvas = document.getElementById("background_canvas");
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;
    }

    // populates the "dots" array
    generateDots() {


        let canvas = document.getElementById("background_canvas"),
            ctx = canvas.getContext("2d");
        const {numDots, minSpeed, maxSpeed, minSize, maxSize, bufferDistance, colors, minAlpha, maxAlpha} = this.state;
        let tempDots = []

        for (let i = 0; i < numDots; i++) {
            let tempRadius = rand(minSize, maxSize);    // lets us use the random value twice
            tempDots[i] = {
                radius: { current: tempRadius, original: tempRadius },
                xpos: rand(canvas.width/2, 2*canvas.width/3),    // starting x position
                ypos: rand(canvas.height/3, 2*canvas.height/3),    // starting y position
                xvelocity: randomDirection() * rand(minSpeed, maxSpeed, false), // x velocity
                yvelocity: randomDirection() * rand(minSpeed, maxSpeed, false), // y velocity
                color: colors[rand(0, rand.length)],
                alpha: rand(minAlpha, maxAlpha, false),

                // draw the dot on the screen
                draw: function () {
                    ctx.beginPath();
                    ctx.arc(this.xpos, this.ypos, this.radius.current, 0, 2 * Math.PI, false);
                    ctx.fillStyle = "rgba(" + this.color + "," + this.alpha + ")"
                    ctx.fill();
                },

                // change xpos and ypos according to the xspeed and yspeed respectively ("bounce" reach buffer)
                move: function () {
                    this.xpos += this.xvelocity;
                    this.ypos += this.yvelocity;

                    if (this.xpos < -1 * bufferDistance) {
                        this.xpos = -1 * bufferDistance
                        this.xvelocity *= -1;
                    } else if (this.xpos > canvas.width + bufferDistance) {
                        this.xpos = canvas.width + bufferDistance
                        this.xvelocity *= -1;
                    }

                    if (this.ypos < -1 * bufferDistance) {
                        this.ypos = -1 * bufferDistance
                        this.yvelocity *= -1;
                    } else if (this.ypos > canvas.height + bufferDistance) {
                        this.ypos = canvas.height + bufferDistance
                        this.yvelocity *= -1;
                    }
                },
            }
        }

        this.setState({dots: tempDots});

        // generates a random number between the min and max (inclusive for integers and exclusive for floating point)
        function rand(min, max, whole = true) {

            if (whole) max++;
            let num = Math.random() * (max - min) + min;
            if (whole) return Math.floor(num);
            return num;
        };

        // randomly returns 1 or -1
        function randomDirection() {

            let forward = Math.floor(Math.random() * 2) === 0;
            if (forward) return 1;
            return -1;
        }
    }

    runDots() {
        let canvas = document.getElementById("background_canvas"),
            ctx = canvas.getContext("2d");

        let intervals = setInterval(() => {
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            this.state.dots.forEach((dot) => {
                dot.draw();
                dot.move();
            });
        }, 50);

        this.setState({intervalId: {dots: intervals}});
    }

    sortDots() {
        let midWidth = this.state.canvas.width / 2;
        let midHeight = this.state.canvas.height / 2;
        this.state.dots.sort((dot1, dot2) => (Math.abs(midWidth - dot1.xpos) + Math.abs(midHeight - dot1.ypos) > Math.abs(midWidth - dot2.xpos) + Math.abs(midHeight - dot2.ypos)) ? 1 : -1);
    }

    render() {

        return (
            <canvas id="background_canvas"></canvas>
        )
    }
}

export default LandingCanvas