import React, { Component } from 'react'
import Job from "./Job"
import "./Employment.css"

class Employment extends Component {

    render() {

        let utreview_logo = require("./../img/utreview_logo.png")
        let equinor_logo = require("./../img/equinor_logo.png")

        return (
            <main id="about-main" className="employment">

                <div className="d-inline">

                    <div className="content-panel content-panel-bg">

                        <div>
                            <h2 className="heading-title">Employment</h2>
                            <p>(hover or tap for more info)</p>
                        </div>

                        <Job logo={equinor_logo} companyName="Equinor" positionName="IT and Data Management Intern" date="June 2020 - July 2020"/>
                        <Job logo={utreview_logo} companyName="UT Review" positionName="Co-Founder, Frontend/Backend Developer" date="May 2020 - Present"/>

                    </div>
                </div>
            </main>
        )
    }
}

export default Employment